import React, { Component } from 'react';
import { Container, Row, Col, Form, Button, Card } from 'react-bootstrap';
import { Redirect } from 'react-router-dom';
import uuidv4 from 'uuid/v4';
import idb_pos_trx from '../../views/post_trx.js';
import Jumbotron from '../../shared/Jumbotron';

class Warung extends Component {
    constructor(props){
        super(props);
        this.state = {
            namaWarung: '',
            kodeWarung: '',
            alamat: '',
            noTelp: '',
            deskripsi: '',
            foto: '',
            redirect: false,
        };
        this.handleClick = this.handleClick.bind(this)
    }

    handleChange = prop => event => {
        this.setState({ [prop]: event.target.value })
    }

    fileUploadChange = prop => event => {
        this.setState({
            foto: event.target.files[0]
        })
    }

    handleClick = event => {
        event.preventDefault()
        const id = uuidv4();
        var datas = {
            'id': id,
            'nama_warung': this.state.namaWarung,
            'kode_warung': this.state.kodeWarung,
            'alamat': this.state.alamat,
            'no_telp': this.state.noTelp,
            'deskripsi': this.state.deskripsi,
        }

        if('serviceWorker' in navigator && 'SyncManager' in window){
            console.log(id);
            console.log(datas);
            console.log('service worker and background sync is ready');
            navigator.serviceWorker.ready
            .then(function(sw){
                idb_pos_trx.set(id, datas)
                .then(function(){
                    sw.sync.register('sync-new-trx');
                });
            });
        }else{
            idb_pos_trx.set(id, datas);
        }
    }

    render() {
        const { redirect } = this.state;
        if(redirect){
            return <Redirect to="/" />
        }
        return(
            <React.Fragment>
                <Jumbotron />
                <div>
                    <Container>
                        <Row justify="center">
                            <Card style={{ width: '100%' }}>
                                <Card.Body>
                                    <Col sm={12}>
                                        <Form id="form_warung">
                                            <Form.Group>
                                                <Form.Label> Nama Warung </Form.Label>
                                                <Form.Control
                                                    id="nama_warung"  
                                                    type="text" 
                                                    placeholder="Input Nama Warung"
                                                    value={this.state.namaWarung}
                                                    onChange={this.handleChange('namaWarung')} 
                                                />
                                            </Form.Group>
                                            <Form.Group>
                                                <Form.Label> Kode Warung </Form.Label>
                                                <Form.Control 
                                                    id="kode_warung"
                                                    as="select"
                                                    value={this.state.kodeWarung}
                                                    onChange={this.handleChange('kodeWarung')}
                                                >
                                                    <option> -- Select -- </option>
                                                    <option key="1" value="0001"> 0001 </option>
                                                    <option key="2" value="0002"> 0002 </option>
                                                    <option key="3" value="0003"> 0003 </option>
                                                    <option key="4" value="0004"> 0004 </option>
                                                    <option key="5" value="0005"> 0005 </option>
                                                </Form.Control>
                                            </Form.Group>
                                            <Form.Group>
                                                <Form.Label> Alamat </Form.Label>
                                                <Form.Control 
                                                    id="alamat"
                                                    placeholder="Input Alamat Lengkap"
                                                    as="textarea" 
                                                    rows="3"
                                                    value={this.state.alamat}
                                                    onChange={this.handleChange('alamat')} 
                                                />
                                            </Form.Group>
                                            <Form.Group>
                                                <Form.Label> No. Telp </Form.Label>
                                                <Form.Control
                                                    id="no_telp" 
                                                    type="text" 
                                                    placeholder="Input No. Telp Warung"
                                                    value={this.state.noTelp}
                                                    onChange={this.handleChange('noTelp')} 
                                                />
                                            </Form.Group>
                                            <Form.Group>
                                                <Form.Label> Deskripsi </Form.Label>
                                                <Form.Control 
                                                    id="deskripsi"
                                                    as="textarea"
                                                    placeholder="Input Deskripsi Warung Anda" 
                                                    rows="3"
                                                    value={this.state.deskripsi}
                                                    onChange={this.handleChange('deskripsi')} 
                                                />
                                            </Form.Group>
                                            <Form.Group>
                                                <Form.Label> Upload Foto </Form.Label>
                                                <Form.Control 
                                                    type="file"
                                                    onChange={this.fileUploadChange()} 
                                                />
                                            </Form.Group>
                                            <Button 
                                                variant="primary" 
                                                type="submit"
                                                onClick={this.handleClick}
                                            >
                                                Submit
                                            </Button>
                                        </Form>
                                    </Col>
                                </Card.Body>
                            </Card>
                        </Row>
                    </Container>
                </div>    
            </React.Fragment>
        );
    }
}

export default Warung;