import React, { Component } from 'react';
import { HashRouter, Route, Switch } from 'react-router-dom';
import CatalogView from './views/Catalog';
import NestedView from './views/Nested';
import WarungView from './views/warung/Warung';
import PostWarungView from './views/warung/postWarung';
import HomeView from './views/Home';
import Nav from './shared/Navbar';
// import Carousel from './shared/Carousel';
import Footer from './shared/Footer';

function NotFound() {
    return(
        <div>
            <h1> 404 Not Found </h1>
            <p> <h3> The Page What Your Request Is Not Found </h3> </p>
        </div>
    );
};

class Indes extends Component {
    render() {
        return(
            <HashRouter>
                <div>    
                    <Nav />
                            <Switch>
                                <Route path="/" exact component={HomeView} />
                                <Route path="/warung" component={WarungView} />
                                <Route path="/create-warung" component={PostWarungView} />
                                <Route path="/catalog" component={CatalogView} />
                                <Route path="/nested" component={NestedView} />
                                <Route component={NotFound} /> 
                            </Switch>
                        <div>
                            <Footer />
                        </div>
                </div>
            </HashRouter>
        );
    }
}

export default Indes;