import * as React from 'react';
import Carousel from '../shared/Carousel'
import { Container, Row, Col, Breadcrumb } from 'react-bootstrap';
import { Redirect, Link } from 'react-router-dom';
import injectSheet from 'react-jss';
import { Lazy } from 'react-lazy';
import { getBerita } from '../shared/Api';

const styles = theme => ({
  wrapper: {
    margin: [[20, 0]],
    padding: 18,
  },
  item: {
    display: 'flex',
    padding: [[18, 0]],
  },
  imageWrapper: {
    flexShrink: 0,
  },
  image: {
    objectFit: 'cover',
    display: 'inline-block',
    backgroundColor: 'rgba(0, 0, 0, 0.067)',
    width: 96,
    height: 54,
    '@media (min-width: 640px)': {
      width: 250,
      height: 140,
    },
    '@media (min-width: 860px)': {
      width: 332,
      height: 186,
    },
  },
  bodyWrapper: {
    paddingLeft: 18,
    flex: '1 1 auto',
  },
  title: {
    composes: 'heading',
    lineHeight: 1,
    '& a': {
      fontSize: 18,
      color: theme.baseColor,
    },
    '@media (min-width: 640px)': {
      lineHeight: 1.5,
      '& a': {
        fontSize: 21,
      },
    },
  },
  excerpt: {
    position: 'absolute',
    opacity: 0,
    pointerEvents: 'none',
    left: '-999em',
    lineHeight: 1.6,
    margin: [[18, 0]],
    '@media (min-width: 640px)': {
      position: 'static',
      opacity: 1,
      pointerEvents: 'auto',
      color: theme.textMuted,
    },
  },
  info: {
    fontSize: 13,
    marginRight: 6,
    color: theme.textMuted,
  },
});

const regex = /(<[^>]+>|&nbsp;)/ig;

class Home extends React.PureComponent {
    constructor(props){
        super(props);
        this.state = {
            data: [],
            redirect: false,
        };
    }

    async componentDidMount(){
        await getBerita().then(res => {
            this.setState({ data: res.data.data })
        })
        .catch(function(error){
            console.log(error)
        });
    }

    render() {
        const { classes } = this.props;
        const { redirect } = this.state;
        if(redirect){
            return <Redirect to="/" />
        }
        return(
            <React.Fragment>
                <Carousel />
                <div>
                    <Container>
                        <Row justify="left">
                            <Col sm={12}>
                                <Breadcrumb>
                                    <Breadcrumb.Item> Home </Breadcrumb.Item>
                                </Breadcrumb>
                            </Col>
                            <Col sm={12}>
                                <div style={{ marginRight: '5%', marginLeft: '5%' }}>
                                    <main>
                                        <div>
                                            {/* End hero unit */}
                                                { this.state.data.map((product) =>
                                                    <div key={product.id} className={classes.item}>
                                                        <div className={classes.imageWrapper}>
                                                          <Link to={`/berita/${product.id}`}>
                                                            <Lazy ltIE9 className={classes.image}>
                                                              <img
                                                                src={product.link_image}
                                                                alt={product.title}
                                                                className={classes.image}
                                                              />
                                                            </Lazy>
                                                          </Link>
                                                        </div>
                                                        <div className={classes.bodyWrapper}>
                                                          <h2 className={classes.title}>
                                                            <Link to={`/berita/${product.id}`}>
                                                              {product.title}
                                                            </Link>
                                                          </h2>
                                                          <div className={classes.excerpt}>
                                                            {product.content=product.content.replace(regex, '').substring(0, 200)}
                                                          </div>
                                                          <div className={classes.info}>
                                                            <span>
                                                              {product.author}
                                                            </span>
                                                          </div>
                                                        </div>
                                                    </div> 
                                                )}
                                        </div>
                                    </main>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </div>    
            </React.Fragment>
        );
    }
}

export default injectSheet(styles)(Home);