import React, { Component } from 'react';

class Footer extends Component {
    render() {
        return(
            <div>
                <br/>
                <footer 
                    className="footer" 
                    style={{ backgroundColor: 'black', height: '100px' }}>
                  <div className="container" style={{ textAlign: 'center', color: 'white', verticalAlign: 'middle', lineHeight: '100px' }}>
                    <span className="text-muted"> Nadzief Corp &copy; 2019 </span>
                  </div>
                </footer>
            </div>
        );
    }
};

export default Footer;