import React, { Component } from 'react';
import { Container, Row, Col, Button, Breadcrumb, Table } from 'react-bootstrap';
import { Redirect, Link } from 'react-router-dom';
import { getWarung } from '../../shared/Api';
import Jumbotron from '../../shared/Jumbotron';

class Warung extends Component {
    constructor(props){
        super(props);
        this.state = {
            data: [],
            redirect: false,
        };
    }

    async componentDidMount(){
        await getWarung().then(res => {
            this.setState({ data: res.data.data })
        })
        .catch(function(error){
            console.log(error)
        });
    }

    render() {
        const { redirect } = this.state;
        if(redirect){
            return <Redirect to="/" />
        }
        return(
            <React.Fragment>
                <Jumbotron />
                <div>
                    <Container>
                        <Row justify="left">
                            <Col sm={12}>
                                <Breadcrumb>
                                    <Breadcrumb.Item> Home </Breadcrumb.Item>
                                    <Breadcrumb.Item active>Warung</Breadcrumb.Item>
                                </Breadcrumb>
                            </Col>
                            <Col sm={12}>
                                <Link to="create-warung">
                                    <Button variant="primary">
                                        <i className="fas fa-user-plus">
                                        </i> &nbsp;
                                        Tambah Warung
                                    </Button>
                                    <br/><br/>
                                </Link>
                            </Col>
                            <Col sm={12}>
                                <Table striped bordered hover responsive>
                                    <thead>
                                        <tr>
                                            <th>Nama Warung</th>
                                            <th>Kode Warung</th>
                                            <th>Alamat</th>
                                            <th>No. Telp</th>
                                            <th>Deskripsi</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.state.data.map(function(item){
                                            return(
                                                <tr key={item.id}>
                                                    <td> {item.nama_warung} </td>
                                                    <td> {item.kode_warung} </td>
                                                    <td> {item.alamat} </td>
                                                    <td> {item.no_telp} </td>
                                                    <td> {item.deskripsi} </td>
                                                </tr>
                                            )
                                        })}
                                    </tbody>
                                </Table>
                            </Col>
                        </Row>
                    </Container>
                </div>    
            </React.Fragment>
        );
    }
}

export default Warung;